import { axiosInstance } from '@/shared/axiosInstance';
import { NextResponse } from 'next/server';

export async function GET() {
  try {
    const response = await axiosInstance.get('http://localhost:5129/top-positions').then(res => res.data).catch(() => []);
    return NextResponse.json(response);
  } catch (error) {
    return NextResponse.json({ error: `Backend error encountered: ${error}` }, { status: 500 });
  }
}